.sidebar-layout__sidebar {
}

.sidebar-layout__sidebar {
  min-height: calc(100vh - var(--header-height));
}

.sidebar-layout__modal-trigger {
}

@media (min-width: 800px) {
  .sidebar-layout__modal-trigger {
    display: none;
  }
}

.sidebar-layout__modal {
  --core-modal-max-width: 500px;
  --core-modal-width: 80%;
  --core-modal-min-width: 200px;
  --core-modal-min-height: 100vh;
  --core-modal-translateY: 0px;
  --core-modal-translateX: -100%;
  --core-modal-justify: start;
}
.sidebar-layout__modal[open] {
  --core-modal-translateY: 0px;
  --core-modal-translateX: 0px;
}
