.seed-card {
  position: relative;
}

.seed-card .bookmark {
  position: absolute;
  right: 0;
  top: 0;
  cursor: pointer;
  color: var(--core-color-font-light);
}

.seed-card .bookmark:hover {
  color: var(--core-color-font);
}

.seed-card .bookmark[checked] {
  color: var(--core-color-primary);
}
