:root {
  --core-color-primary-hue: 130;
  --core-color-primary-light: hsl(var(--core-color-primary-hue), 100%, 40%);
  --core-color-primary: hsl(var(--core-color-primary-hue), 100%, 38%);
  --core-color-primary-dark: hsl(var(--core-color-primary-hue), 100%, 30%);

  --core-color-ui: hsl(var(--core-color-primary-hue), 0%, 82%);
  --core-color-ui-light: hsl(var(--core-color-primary-hue), 0%, 90%);
  --core-color-ui-lighter: hsl(var(--core-color-primary-hue), 0%, 95%);

  --core-border-radius-default: 6px;
  --core-border-width-default: 2px;
}

[mode="dark"] {
  --core-color-primary-hue: 130;
  --core-color-white: hsl(var(--core-color-ui-hue), 10%, 10%);
}

@media (max-width: 800px) {
  /* Full height on mobile */
  core-modal {
    --core-modal-min-height: 100%;
    --core-modal-max-height: 100%;
    --core-modal-max-width: 100%;
  }
}

@media (min-width: 800px) {
  core-tabs.view-mode {
    display: none;
  }
}

/* CORE RANGE */

core-range {
  --core-range-label-font-size: var(--core-font-size-xs);
  --core-range-label-text-transform: uppercase;
}

/* CORE LIST */

core-list-item {
  border-radius: var(--core-border-radius-default);
}

core-list[selectable] core-list-item:hover:not([selected]) {
  background-color: var(--core-color-ui-lighter);
}

core-list-item[selected] {
  background-color: var(--core-color-primary);
  color: var(--core-color-white);
}

/* CORE SELECT */

core-select::part(list) {
  border-radius: 5px;
}

core-select[multiple] core-option[selected] {
  display: none;
}

core-select[size="lg"] {
  --core-select-min-height: var(--core-size-lg);
  --core-select-padding: 0 var(--core-space-sm);
}

/* COrE TABS */

core-tabs.view-mode {
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  z-index: 999;
  bottom: calc(var(--header-height) + var(--core-space-sm));
}

core-tabs.view-mode core-button:first-of-type {
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  border-right: 0;
}

core-tabs.view-mode core-button:last-of-type {
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
}

/* CORE BUTTON */

core-button[type="secondary"] {
  background: var(--core-color-white);
  color: var(--core-font-color);
  border: 2px solid var(--core-color-ui-light);
}

core-button[type="secondary"]:focus {
  border-color: var(--core-color-focus);
}

core-label {
  margin-bottom: var(--core-space-md);
  color: var(--core-color-font-light);
}

core-tabs core-button[selected] {
  background-color: var(--core-color-primary);
  color: var(--core-color-white);
}

core-accordion.advanced::part(trigger):hover {
  color: var(--core-color-font-dark);
}

core-button[size="xl"] {
  height: var(--core-size-xl);
  padding: 0 var(--core-space-lg);
}
