body {
  padding: 0;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: var(--core-font-family);
  --header-height: 70px;
}

body {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.App {
  position: relative;
  min-height: 100vh;
}

hr {
  margin: 0;
  padding: 0;
}

#home {
  min-height: 100vh;
  background: url("./assets/bg.png");
  background-size: 400px;
  background-repeat: no-repeat;
  background-position: bottom right;
}

@media (min-width: 800px) {
  #home {
    background-size: 600px;
  }
}

.mapContainer {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

.mapboxgl-canvas-container {
  height: calc(100vh - var(--header-height));
  width: 100%;
}

a {
  color: currentColor;
  text-decoration: none;
}

.table-wrapper {
  max-width: 100%;
  width: 100%;
  overflow-x: scroll;
}

.main-heading {
  font-size: 45px;
}

table {
  position: relative;
  width: 100%;
  max-width: 100%;
  border-collapse: collapse;
  border-radius: 4px;
  border-left: 0;
  text-align: left;
}

table thead th,
table thead td {
  position: sticky;
  top: 0;
  background: var(--core-color-white);
  z-index: 10;
  border-bottom: 2px solid var(--core-color-ui-light);
  padding: var(--core-space-lg);
}

table tbody td {
  padding: var(--core-space-lg);
  border-bottom: 1px solid var(--core-color-ui-light);
}

table tbody tr:first-of-type td {
  border-top: 1px solid var(--core-color-ui-light);
}

table.clickable tbody tr:hover {
  cursor: pointer;
  background: var(--core-color-ui-lightest);
}

.align-center {
  text-align: center;
}

.align-right {
  text-align: right;
}
