.header {
  --header-height: 70px;
  border-top: 1px solid var(--core-color-ui-light);
  background: var(--core-color-white);
  position: fixed;
  bottom: 0;
  z-index: 500;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0;
  height: var(--header-height);
}

.header .logo {
  display: none;
}

.header nav {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.header nav .header__menu-item {
  color: var(--core-color-font);
  height: var(--core-size-md);
  text-align: center;
  display: inline-flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  text-align: center;
  margin-left: 0;
}

.header nav .header__menu-item:hover {
  color: var(--core-color-font-dark);
}

.header nav .header__menu-item.active {
  color: var(--core-color-primary);
}

.header nav span {
  font-size: var(--core-font-size-xs);
  padding-top: var(--core-space-sm);
  display: block;
}

.header nav i {
  margin-right: 0;
}

@media (min-width: 800px) {
  .header {
    --header-height: 60px;
    position: relative;
    top: 0;
    padding: 0 var(--core-space-lg);
  }

  .header .logo {
    display: block;
  }

  .header nav {
    width: auto;
  }

  .header nav span {
    display: inline-block;
  }

  .header nav .header__menu-item {
    margin-left: var(--core-space-lg);
    height: var(--core-size-md);
    flex-direction: row;
  }

  .header nav i {
    margin-right: var(--core-space-md);
  }

  .header nav span {
    padding-top: 0;
  }
}
