.map-search-input {
  --core-select-min-height: var(--core-size-md);
}

.map-search-input [slot="start"] {
  margin-left: var(--core-space-sm);
}

.map-search-input[size="md"] {
  --core-select-min-height: var(--core-size-md);
}

.map-search-input[size="lg"] {
  --core-select-min-height: var(--core-size-lg);
}

.map-search-input[size="xl"] {
  --core-select-min-height: var(--core-size-xl);
}

.map-search-input[size="xl"] [slot="start"] {
  margin-left: var(--core-space-md);
}
